	module.exports = {"data":{"talkJson":{"description":"Rich Hickey, author of Clojure, and designer of Datomic presents a new way to look at database architectures in this talk from JaxConf 2012.","meta":{"duration":"PT1H6M24S"},"presenter":{"name":"Rich Hickey"},"slug":"deconstructing-the-database","source":{"id":"Cym4TZwTCNU","provider":"youtube"},"tag":["Database","JaxConf 2012","Architecture"],"title":"Deconstructing the Database"},"allTalkJson":{"edges":[{"node":{"description":"In this keynote speech from JaxConf 2012, Rich Hickey, creator of Clojure and founder of Datomic gives an awesome analysis of the changing way we think about values (not the philosphoical kind) in light of the increasing complexity of information technology and the advent of Big Data. The broad subject of the talk makes it worth watching for almost anyone in the programming world, and was one of the highlights of the JaxConf lineup.","meta":{"duration":"PT31M44S"},"presenter":{"name":"Rich Hickey"},"source":{"id":"-6BsiVyC1kM","provider":"youtube"},"slug":"the-value-of-values","tag":["JaxConf 2012","Keynote"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/-6BsiVyC1kM/default.jpg","height":90,"width":120}},"title":"The Value of Values"}},{"node":{"description":"Everything is changing. Everything is new. Frameworks, platforms and trends are displaced on a weekly basis. Skills are churning.\n\nAnd yet... Beneath this seemingly turbulent flow there is a slow current, strong and steady, changing relatively little over the decades. Concepts with a long history appear in new forms and fads and technologies. Principles are revisited. Ideas once lost to the mainstream are found again.\n\nIn this keynote we revisit the present through the past, looking at the enduring principles that shape programming languages, architecture, development practice and development process, the ideas that cycle round, each time becoming perhaps a little better defined, a little more mature, and look to see what else might be on the horizon.","meta":{"duration":"PT50M23S"},"presenter":{"name":"Kevlin Henney"},"source":{"id":"AbgsfeGvg3E","provider":"youtube"},"slug":"old-is-the-new-new","tag":["GOTO Conference","Keynote","Architecture","Languages"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/AbgsfeGvg3E/default.jpg","height":90,"width":120}},"title":"Old Is the New New"}}]}},"pathContext":{"slug":"deconstructing-the-database","tags":["Database","JaxConf 2012","Architecture"]}}

